          <p>–í –∫–∞—á–µ—Å—Ç–≤–µ –±–ª–∞–≥–æ–¥–∞—Ä–Ω–æ—Å—Ç–∏, –º—ã –Ω–∞—á–∏—Å–ª–∏–º –≤–∞–º –±–æ–Ω—É—Å 100<span><img class="bonus-chip" src="/img/crystal.png" width="14px"/></span></p>
          <p style="text-align:center">
