<script>
import {
  IonAccordionGroup,
  IonAccordion,
  IonLabel,
  IonChip,
  IonItem,
  IonGrid,
  IonRow,
  IonCol,
  IonText,
  IonCard,
  IonCardHeader,
  IonCardTitle,
  IonCardContent,
  IonIcon,
  IonButton,
}                         from "@ionic/vue";
import {  
  timeOutline, 
  searchOutline,
  arrowRedoOutline
 }                        from 'ionicons/icons'
 import { Share }         from '@capacitor/share';
import StatisticsCard     from "@/components/StatisticsCard.vue";

export default {
  components: {
    IonAccordionGroup,
    IonAccordion,
    IonLabel,
    IonChip,
    IonItem,
    IonGrid,
    IonRow,
    IonCol,
    IonText,
    IonCard,
    IonCardHeader,
    IonCardTitle,
    IonCardContent,
    IonIcon,
    IonButton,
    StatisticsCard
  },
  setup(){
      return {
        timeOutline,
        searchOutline,
        arrowRedoOutline
      }
  },
  data() {
    return {
      dataset: {},
      bonusTotal: 0,
      statisticsDates: []
    };
  },
  computed: {
  },
  methods: {
    async listGet(){
        try{
            const request = {
                user_id: this.$heap.state.user.user_id,
                point_span: 3,
                point_num: 10
            }
            const response = await this.$post(`${this.$heap.state.hostname}Statistics/bonusParametersGet`, request)
            response.body.reverse()
            this.bonusTotal = response.head.bonus_total??0
            this.statisticsDates = this.parseDates(response)
            this.parseStatisticResponse(response)
        }catch{/** */}
    },
    parseStatisticResponse(response){
        const bars = {
          bonus_gained: true,
          bonus_spent: true
        }
        for(var param in bars){
          let dataset={
            [param]:{
              value: 0,
              series: []
            }
          }
          for(var i in response.body){
            if(response.body[i][param]){
              dataset[param].series.push(response.body[i][param])
              dataset[param].value += response.body[i][param]*1
            }
          }
          if(dataset[param].series.length){
            this.dataset=dataset
          }
        }
    },
    parseDates(response){
        let result = [];
        for(var i in response.body){
            result.unshift(response.body[i].point_finish)
        }
        return result
    },
    async shareInvitation(){
            try{
                const targetTitle=this.$heap.state.settings.app_title
                const targetText='–ü—Ä–∏—Å–æ–µ–¥–∏–Ω—è–π—Ç–µ—Å—å –∏ –ø–æ–ª—É—á–∏—Ç–µ –±–æ–Ω—É—Å'
                const canshare=await Share.canShare()
                const fullUrl=`${this.$heap.getters.settings.app.frontendUrl}invitation.html?inviter_user_id=${this.$heap.state.user.user_id}`
                if(canshare){
                    await Share.share({
                        title: targetTitle,
                        text: targetText,
                        url: fullUrl,
                        dialogTitle: targetTitle,
                    });
                } else if(navigator.clipboard){
                    await navigator.clipboard.writeText(fullUrl);
                    this.$alert("–¢–µ–ø–µ—Ä—å –≤—ã –º–æ–∂–µ—Ç–µ –ø–æ–¥–µ–ª–∏—Ç—å—Å—è –µ–π —Å –¥—Ä—É–∑—å—è–º–∏ –≤ —Å–æ—Ü–∏–∞–ª—å–Ω—ã—Ö —Å–µ—Ç—è—Ö –∏–ª–∏ –º–µ—Å—Å–µ–Ω–¥–∂–µ—Ä–∞—Ö.","–°—Å—ã–ª–∫–∞ –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É —Å–∫–æ–ø–∏—Ä–æ–≤–∞–Ω–∞");
                } else {
                    this.$alert("–£—Å—Ç—Ä–æ–π—Å—Ç–≤–æ –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç —Ñ—É–Ω–∫—Ü–∏—é –ø–æ–¥–µ–ª–∏—Ç—å—Å—è",""); 
                }
            }catch(err){
                //console.log(err)
            }
        },
  },
  mounted(){
    this.listGet();
  },
  activated(){
    this.listGet();
  }
};
</script>

